import {Badge, Button} from "react-bootstrap";
import {switchVisibleChangePasswordModal, switchVisibleEditProfileModal} from "../store";

export const Profile = () => {
    return <>
        <div>
            <h6>:</h6>
        </div>
        <div className="d-block d-lg-flex justify-content-around">
            <div className="flex-fill">
                <div>
                    <span>:</span> 
                </div>

                <div>
                    <span>:</span> test@gmail.com
                </div>
            </div>
            <div className="flex-fill">
                <div>
                    <span> : </span>{new Date().toLocaleDateString()}
                </div>

                <div>
                    <span>: </span>
                    <Badge bg="success"> ( {new Date().toLocaleDateString()})</Badge>
                </div>
            </div>
        </div>
        <div className="d-flex mt-2">
            <Button className="me-2" variant="primary" size="sm" onClick={switchVisibleEditProfileModal}></Button>
            <Button variant="primary" size="sm" onClick={switchVisibleChangePasswordModal}> </Button>
        </div>
        <hr/>
    </>

}
